.projectsContainer{
    height: 2500px;
    background-color: aquamarine;
}
.titleText{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    font-weight: 700;
    text-decoration: underline;
    height: 10%;
}
.contextContainer{
    height: 90%;
}
.contextBox{
    background-color: aliceblue;
    width: 1000px;
    height: 650px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4); 
    cursor: pointer;
    transition: transform 0.3s ease-in-out; 
    margin-bottom: 100px;
}

.contextBoxend{
    background-color: aliceblue;
    width: 1000px;
    height: 700px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4); 
    cursor: pointer;
    transition: transform 0.3s ease-in-out; 
}
.contextboader{
    display: flex;
    justify-content: center;
    align-items: center;
}
.contextBox:hover{
    transform: scale(1.05); 
}
.imageBox{
    float: left;
    width: 50%;
    height: 100%;
}
.imageBox img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* or "contain" depending on your preference */
}

.textBox{
    float: left;
    width: 50%;
    height: 100%;
}

.nameText{
    margin: 10px;
    font-size: 30px;
    font-weight: bold;
}
.nameText2{
    margin: 20px;
    font-size: 20px;
    font-weight: 400;
}
.descriptionText{
    font-size: 16px;
    font-weight: 600;
    margin: 10px;
}
.miniBox{
    width: 100%;
    margin: 10px;
    padding: 5px;
}

.minicontentsLeft{
    font-size: 18px;
    font-weight: 700;
}
.minicontentsRight{
    font-size: 16px;
    margin: 5px;
}

.minicontentsLink {
    color: #007bff; 
    font-size: 18px; 
    text-decoration: none; 
}

.minicontentsLink:hover {
    text-decoration: underline; 
   
}
.css-pl8gqh-MuiStack-root{
    display: none !important;
}