.skillContaier{
    width: 100%;
    height: 1000px;
    margin-top: 80px;
    background-color: rgb(42, 212, 212);
}
.titleBox{
    height: 25%;
    font-size: 60px;
    font-weight: 700;
    text-decoration-line: underline;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}
.contentBox{
    height: 75%;
}
.titletext{
    font-size: 30px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}
.imageBoxlist{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.imageBox{
    float: left;
    background-color: azure;
    border-radius: 10px;
    width: 120px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4); 
    transition: transform 0.3s ease-in-out; 
    margin: 10px;
}
.imageBox:hover {
    transform: scale(1.1); 
}
.imageBox img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover; /* or "contain" depending on your preference */
}