.ArchivingContaier{
    width: 100%;
    height: 600px;
    margin-top: 80px;
}
.titletext{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    font-weight: 700;
    text-decoration: underline;
}
.contents{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}
.contentsBox{
    width: 400px;
    height: 300px;
    border-radius: 20px;
    background-color:rgb(220, 229, 238);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4); 
    transition: transform 0.3s ease-in-out; 
    cursor: pointer;
    margin: 40px;
}
.contentsBox:hover{
    transform: translateY(20px);
}

.contentsBox a {
    color: rgb(43, 0, 235);
    margin: 10px;
    font-size: 20px;
}
.contentsText{
    margin: 10px;
    font-size: 16px;
    font-weight: bold;
}


.contentsBox li {
    font-size: 16px; /* Adjust the font size to your preference */
    margin-bottom: 8px; 
}

.imageBox{
    width: 100px;
    height: 100px;
    margin: 10px;
}

.imageBox img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover; /* or "contain" depending on your preference */
}