* {
    box-sizing: border-box;
}
.aboutContaier{
    width: 100%;
    height: 600px;
    margin-top: 80px;
}

.titleText{
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 60px;
    font-weight: 700;
    text-decoration: underline;
}

.contentsBox{
    width: 100%;
    height: 80%;
    margin-top: 50px;
}
.contentBox2{
    width: 100%;    
    height: 50%;
    float: left;
}
.content{
    float: left;
    width: 33%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.iconBox{
    float: left;
    width: 50%;
    margin-right: 50px;
    display: flex;
    justify-content: end;
}
.textBox{
    float: left;
    width: 50%;
}
.aboutText1{
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 20px;
}
.aboutText2{
    font-size: 20px;
}

@media screen and (max-width: 1250px) {
    .contentBox2{
        float: none;
    }
    .content {
        width: 100%;
        height: auto;
    }

    .iconBox{
        width: 100%;
    }
    .textBox{
        width: 100%;
    }
    .titleText{
        display: none;
    }
    
}