html {
  scroll-behavior: smooth;
}
.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #082346;
  color: white;
  padding: 10px;
  position: fixed;
  width: 100%;
  height: 50px;
  top: 0;
  left: 0;
  z-index: 1000;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2), 0px 4px 6px rgba(0, 0, 0, 0.1); 
}

.navbar div {
  margin-left: 200px;
}

.navbartext {
  font-size: 30px;
  font-weight: 600;
  cursor: pointer;
}

.navbar ul {
  list-style: none;
  display: flex;
  margin-right: 200px;
  padding: 0;
}

.navbar li a{
  font-size: 20px;
  font-weight: 600;
  margin-right: 40px;
  cursor: pointer;
  transition: color 0.3s, font-size 0.3s; 
  text-decoration: none; /* Remove underline */
}

.navbar li a:hover {
  color: skyblue !important;
  font-size: 24px; 
}

.navbar ul li a:visited {
  color: white; 
}

.reorderIconWrapper {
  display: block;  /* 일단 숨김 처리 */
  cursor: pointer;
}
.clicknavbar{
  /* display: none; */
  margin-top: 50px;
  background: #082346;
  width: 100%;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2), 0px 4px 6px rgba(0, 0, 0, 0.1); /* Updated box shadow */
  z-index: 1001;
}
.clicknavbar div {
  margin-top: 10px;
}
.clicknavbar div a{
  font-size: 30px;
  color: white;
  font-weight: 600;
  text-decoration: none;
}
.clicknavbar div a:visited{
  color: white;
}

.clicknavbar div a:hover{
  color: skyblue !important;
  font-size: 35px; 
}
@media (max-width: 1250px) {
  .navbar ul {
    display: none;  
  }
}

@media (min-width: 1250px) {
  .reorderIconWrapper {
    display: none;  
  }
}

@media (min-width: 1250px) {
  .clicknavbar {
    display: none;
  }
}



.title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 104vh;
  background-image: url("/public/assets/title.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  filter: brightness(0.8);
}

.titleText {
  display: flex;
  align-items: center;
  font-size: 80px;
  font-weight: 600;
  color: #fff;
  filter: none;
}
